

.about-us-container {
  display: grid;
  grid-template-columns: repeat(2, auto);
  justify-content: space-between;
  align-items: center;
  gap: 5%;
  flex-wrap: wrap;
  padding: 50px 30px 30px 10px;
  font-family: Arial, sans-serif;
  min-height: 100vh !important;
  height:auto;
  background-image: linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%);
  color:#000000 ;
  @media(max-width:786px){
      background: #1f2022!important;
/*     background: #0e8dbc !important;
 */    color:#ffffff !important
  

  } 
  
}

.left-section {
  min-width:100%;
  max-width: 700px;
}

.left-section h2 {
  font-size: 36px;
  font-weight: bold;
  margin-bottom: 20px;
  color: rgba(247, 4, 98, 0.94) !important; }

.left-section p {
  font-size: 18px;
  font-family: Arial, sans-serif;   
  line-height: 5vh !important;
  margin-bottom: 40px;



  @media(max-width:786px){
    line-height: 4.5vh !important;
    min-width:90vw !important;
    position: relative;
    left: -1rem !important;
    padding-right: 1.5rem !important;
    color:#ffffff !important;
    h2{
      left: -1rem !important;
    }

  }
}

  .sign-up-button {
  transform: translateY(-45%) !important;

}



.right-section {
  min-width:100%;
  max-width: 600px;
  @media(max-width:550px){
    max-width: 100vw !important;
    min-width: 100vw !important;
    background: #fa8484 !important;
    padding:0;

  }
}
 
  .card{

    margin-top:0rem;
    border-radius: 5px;
    padding: 20px;
    border: none !important;
    color: #f2f2f2;
    max-width: 450px !important;
    min-width: 450px !important;
    font-weight:400;
    background: linear-gradient(45deg, #141e30, #243b55);
    @media(max-width:786px){
     display: block  !important;
     position: relative;
     left:-8rem !important;
     
    }
    @media(max-width:550px){
     display: block  !important;
     position: relative;
     left:-6rem !important;
     display: none !important;
     
    }


    /* background: #561d5e;  */
 h5{
    margin-left: -5px !important;
  span{
    
    font-size: 1.3rem !important;
    font-family: Arial, Helvetica, sans-serif;
 }
}
 
  }


.card ul {
list-style-type: none;
font-size: 1rem;
margin: 0;
padding: 0;

}
.card li {
  margin-bottom: 10px;
  color: #ffffff;
  font-weight: 500;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  min-width:10px;
  padding:3px;
  border:1px solid #ccc;
  box-shadow: 0px 10px 15px -3px rgba(0,0,0,0.1),0px 10px 15px -3px rgba(0,0,0,0.1);
 
    }

    .card li::before {
        content: '';
        display: inline-block;
        height: 10px;
        width: 10px;
        background: rgba(247, 4, 98, 0.94);
        background-size: contain;
        margin-right: 12px;
    }


.card a {
  color: #007bff;
  text-decoration: none;
}

.card li:hover {
    cursor:pointer;
  text-decoration: underline;
}

@media (max-width: 992px) {
  .about-us-container {
    grid-template-columns: repeat(1, auto) !important;   
  }
  
  .left-section {
    width: 50%;
    margin-bottom: 0;
  }
  
  .right-section {
    width: 50%;
    transform: translate(18%, -10%) !important;
  }


}
@media (max-width: 550px) {
  .about-us-container {
    padding: 60px 30px 15px 10px;

  }
  .left-section {
    max-width: 100% !important;
    min-width: 100% !important;
    padding-right: 2.5rem !important;


    
  }
  .sign-up-button {
    font-size: 1.3rem !important;
    font-family: Arial, Helvetica, sans-serif !important;
    font-weight: bold;
  }

}




